<template>
  <!-- 车辆信息 -->
  <div class="container">
    <div class="message">
      <div class="preview">
        <h4>编辑消息内容</h4>
        <div class="carInfo">
          <div class="item itemAll">
            <div>消息标题</div>
            <el-input v-model="input" placeholder="请输入内容"></el-input>
          </div>
          <div class="item itemAll">
            <div>推送内容</div>
            <el-input
              type="textarea"
              autosize
              placeholder="请输入内容"
              v-model="textarea1"
            >
            </el-input>
          </div>
          <div class="item itemAll">
            <div>备注</div>
            <el-input
              type="textarea"
              autosize
              placeholder="请输入内容"
              v-model="textarea1"
            >
            </el-input>
          </div>
        </div>
      </div>
      <div class="preview">
        <h4>消息预览</h4>
        <div class="previewCon">
          <h5 class="title">消息预览</h5>
          <div class="item">
            <div>处理时间：</div>
            <div>2020.05.05</div>
          </div>
          <div class="item">
            <div>处理结果：</div>
            <div>内容内容内容内容内容</div>
          </div>
        </div>
      </div>
    </div>
    <div class="selNode">
      <el-transfer
        style="text-align: left; display: inline-block"
        v-model="value4"
        filterable
        :left-default-checked="[2, 3]"
        :right-default-checked="[1]"
        :titles="['用户列表', '已选列表']"
        :button-texts="['到左边', '到右边']"
        :format="{
          noChecked: '${total}',
          hasChecked: '${checked}/${total}',
        }"
        @change="handleChange"
        :data="data"
      >
        <span slot-scope="{ option }"
          >{{ option.key }} - {{ option.label }}</span
        >
        <!-- <el-button class="transfer-footer" slot="left-footer" size="small"
          >操作</el-button
        >
        <el-button class="transfer-footer" slot="right-footer" size="small"
          >操作</el-button
        > -->
      </el-transfer>
    </div>
    <div class="carInfo">
      <div class="item itemAll">
        <el-checkbox v-model="checked">定时推送</el-checkbox>
      </div>
      <div class="item itemAll">
        <el-checkbox v-model="checked">推送给所有人</el-checkbox>
      </div>
    </div>
    <div class="elStepsCon">
      <el-button type="primary">发送</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "carInfo",
  data() {
    const generateData = (_) => {
      const data = [];
      for (let i = 1; i <= 15; i++) {
        data.push({
          key: i,
          label: `用户 ${i}`,
          disabled: i % 4 === 0,
        });
      }
      return data;
    };
    return {
      data: generateData(),
      value: [1],
      value4: [1],
      renderFunc(h, option) {
        return (
          <span>
            {option.key} - {option.label}
          </span>
        );
      },
    };
  },
  methods: {
    next() {
      if (this.active++ > 2) this.active = 0;
    },
  },
};
</script>
<style scoped>
.container {
  background-color: #fff;
  height: 100%;
  padding: 20px;
}
.message {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.preview {
  width: 50%;
}
.previewCon {
  padding-top: 30px;
  width: 100%;
  height: auto;
}
.previewCon .title {
  font-size: 16px;
  font-weight: 500;
  padding: 10px;
  border-bottom: 5px solid #999;
}
.previewCon .item {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: 14px;
  color: #999;
  padding: 10px;
}
.elStepsCon {
  width: 400px;
  margin: 20px auto;
  text-align: center;
}
.carInfo {
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
}
.carInfo .item {
  width: 100%;
  padding: 10px 0;
  font-size: 15px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.itemAll img {
  width: 150px;
  height: 150px;
}
.itemAll .el-upload-list {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.carInfo .item div {
  width: 20.33333333%;
  font-size: 15px;
}
.carInfo .item .el-input,
.carInfo .item .el-textarea {
  width: 400px;
  color: #999;
}
</style>


